.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba($color: $color-black, $alpha: 0.7);
  cursor: pointer;
  z-index: 100;
}

.overlay--shown {
  display: flex;
  overflow-y: auto;
}

.overlay--shown .overlay__logo {
  position: absolute;
  top: 0;
  left: 0;
}
