.socials {
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-width: 216px; }

.socials__item {
  margin-right: 16px; }
  .socials__item:last-child {
    margin-right: 0; }

.socials__link {
  color: #cdcdcd;
  transition: all ease-in-out 0.3s; }
  .socials__link:hover, .socials__link:focus, .socials__link:active {
    color: #00b2ff;
    transition: all ease-in-out 0.3s; }

.popup {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px 39px;
  width: 490px;
  background-color: #00b2ff;
  z-index: 100;
  cursor: auto; }

.popup h2 {
  margin: 0;
  margin-bottom: 11px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 140.62%;
  color: #ffffff;
  cursor: default; }

.popup p {
  padding-right: 10px;
  margin: 0;
  margin-bottom: 33px;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  cursor: default; }

.popup__close {
  position: absolute;
  top: 18px;
  right: 14px;
  color: #ffffff;
  transition: all ease-in-out 0.3s; }
  .popup__close:hover, .popup__close:focus, .popup__close:active {
    color: #000000;
    transition: all ease-in-out 0.3s; }

.popup--opened {
  display: block; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  z-index: 100; }

.overlay--shown {
  display: flex;
  overflow-y: auto; }

.overlay--shown .overlay__logo {
  position: absolute;
  top: 0;
  left: 0; }
